import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SettingsComponent } from './settings.component';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';

const routes: Routes = [
  {
    path: '',
    component: SettingsComponent,
  },
  {
    path: 'configuration',
    loadChildren: () => import('./configuration/configuration.module').then(m => m.ConfigurationModule)
  },
  {
    path: 'store-info',
    loadChildren: () => import('./store-info/store-info.module').then(m => m.StoreInfoModule)
  },
  {
    path: 'chat',
    loadChildren: () => import('./chat/chat.module').then(m => m.ChatModule)
  },
  {
    path: 'video',
    loadChildren: () => import('./video/video.module').then(m => m.VideoModule)
  },
  {
    path: 'e-invoice',
    loadChildren: () => import('./e-invoice/e-invoice.module').then(m => m.EInvoiceModule)
  },
  {
    path: 'purchase',
    loadChildren: () => import('./purchase/purchase.module').then(m => m.PurchaseModule)
  },
  {
    path: 'order-interface',
    loadChildren: () => import('./order-interface/order-interface.module').then(m => m.OrderInterfaceModule)
  },
]

@NgModule({
  declarations: [SettingsComponent],
  imports: [
    CommonModule,
    IonicModule,
    TranslateModule,
    RouterModule.forChild(routes)
  ]
})
export class SettingsModule { }
